@import url('https://fonts.googleapis.com/css?family=Lato|Open+Sans:300,300i,400,400i,600,600i,700,800');

body {
	font-family: Open Sans, Lato, Helvetica, sans-serif;
	font-size: 18px;
	line-height: 1.2em;
	font-weight: 300;
	color: $black;
}

h1, h2, h3, h4, h5 {
	line-height: 200%;
}
h1 {
	font-size: 44px;
	font-weight: 300;
}

h2 {
	font-size: 36px;
	font-weight: 300;
}

h3 {
	font-size: 30px;
	font-weight: 400;
}

h4 {
	font-size: 24px;
	font-weight: 300;
}

h5 {
	font-size: 18px;
	font-weight: 500;
}

h6 {
	font-size: 18px;
	font-weight: 500;
}

p {
	margin-top: 40px;
	margin-bottom: 50px;
}

