.featured-image {
	width: 100%;
}
.featured-image > img {
	width: 100%;
	height: 330px;
	overflow: hidden;
	padding: 0 !important;
	margin: 0 !important;
}
.featured-post {
	margin-bottom: 70px;
	
	h1.lead-title {
		margin-top: 70px;
		margin-bottom: 15px;
	}

	.post-meta{
		margin-top: 15px;
		min-width: 100%;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		margin-bottom: 40px;

		.posted-left {
			text-align: left;
			
		}
		.posted-right {
			text-align: right;
			
		}
	}
	.featured-post-content {
		font-size: 22px;
		line-height: 40px;
	}
}