.pull-left {
	float: left;
}

.pull-right {
	float: right;
}
.featured-post img {
	padding: 15px;
}

// Quotes
blockquote,
q {
	display: inline-block;
	margin-top: 50px;
	margin-bottom: 50px;
	min-width: 100%;

	font-style: italic;
	font-weight: 300;
	font-size: 24px;
	line-height: 40px;
	text-align: center;
	.author {
		display: inline-block;
		width: 100%;
		font-weight: 400;
		color: $red;
	}
}