
//Main logo
.main-logo {
	margin-right: 30px;
}

.main-navigation {
	margin-top: 70px;
	margin-bottom: 100px;

	.nav-link {
		color: $black;
		text-decoration: none;
		font-weight: 400;
		margin: 15px;
		font-size: 18px;

		&:hover,
		&.active {
			color: $red;
			font-weight: 400;
		}

	}

	.main-search > input[type=search] {
		text-align: right;

	}
}
.main-search {
	border-left: 1px solid $gray;
	padding-left: 15px;
	padding-right: 15px;
	.search-btn i {
		font-size: 24px;
	}
}