.ghost-btn {
	border: 2px solid $red;
	padding: 20px 28px;
	text-transform: uppercase;
	font-weight: 400;
	text-decoration: none;
	display: inline-block;

	&:hover {
		text-decoration: none;
		color: #fff;
		background: $red;
		box-shadow: 0 4px 8px lighten($black,50%);
	 	transform: translate3d(0,-4px,0);
	}
}