.post-wrap {
	margin-top: 70px;

	.lead-title {
		margin-bottom: 70px;
	}

}
.blog-post {
	margin-bottom: 30px;
}
.post-container {
	padding-top: 30px;
	padding-bottom: 30px;

	.lead-title {
		margin-bottom: 30px;
	}

	.post-meta {
		font-size: 16px;
		min-width: 100%;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		margin-bottom: 30px;
	}


	.post-content > p{
		margin-top: 0;
		font-size: 18px;
		line-height: 34px;
		margin-bottom: 55px;
	}


}