.footer-underline {
		display: block;
		width: 100%;
		height: 20px;
		background: $red;	
}

.footer-content {
	display: flex;
	align-self: center;
	font-size: 18px;
	line-height: 36px;
	padding-top: 50px;
	padding-bottom: 50px;

	.posted-right {
		justify-content: start;
	}
	.posted-left {
		justify-content: end;
		text-align: right;
	}
}